.container {
  max-width: 320px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 15px;

  @media(min-width: 768px) {
    max-width: 768px;
  }

  @media(min-width: 1024px) {
    max-width: 1024px;
  }

  @media(min-width: 1140px) {
    max-width: 1140px;
  }

  @media(min-width: 1260px) {
    max-width: 1260px;
  }
}
